/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable react-hooks/exhaustive-deps */
/**
 * @filename: Header.js
 * @description: 예매 페이지 상단 메뉴
 */
import React, { memo, useCallback, useState } from "react";
import styled from "styled-components";
import { Link, useNavigate } from "react-router-dom";
import { useEffect } from "react";

const ProHeaderWrap = styled.div`
  position: relative;
  z-index: 200;
  width: 100%;
  background: #fff;
  overflow: hidden;
  transition: all 0.4s ease;
  max-height: 60px;
  box-shadow: 0px 3px 2px -2px rgb(0 0 0 / 40%);
  &.open {
    max-height: 400px;
  }
  &::after {
    content: "";
    position: absolute;
    top: 60px;
    left: 0;
    right: auto;
    width: 100%;
    display: block;
    border-top: 1px solid #ddd;
  }
  nav {
    position: relative;
    z-index: 25;
    /* display: flex; */
    /* justify-content: center; */
    &:before {
      content: "";
      display: block;
      position: absolute;
      z-index: -1;
      background: #f5f5f5;
      box-shadow: 0px 3px 2px -2px rgb(0 0 0 / 40%);
      width: 100%;
      height: 95%;
      top: 61px;
    }
    .gnb {
      width: 670px;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      &::after {
        content: "";
        clear: both;
        display: block;
      }
      .gnbList {
        position: relative;
        float: left;
        width: 30%;
        text-align: center;
        &:last-child {
          margin-right: 0;
        }
        .onedepth {
          cursor: pointer;
          position: relative;
          display: block;
          padding: 18px 0;
          font-size: 16px;
          font-weight: 700;
          line-height: 1.5;
          color: #222;
          &:hover {
            color: #e12f36;
          }
          &::after {
            content: "";
            position: absolute;
            top: auto;
            bottom: 0;
            left: 0;
            right: auto;
            width: 0;
            display: block;
            background: #2f7d4e;
            height: 3px;
            transition: width 0.4s ease;
            overflow: hidden;
          }
        }
        .twodepth {
          padding: 20px 0 40px;
          text-align: center;
          li {
            a {
              padding: 5px 0;
              display: block;
              font-size: 14px;
              color: #505050;
              line-height: 1.5;
              text-align: center;
              &:hover {
                color: #e12f36;
              }
            }
          }
        }
      }
      a.logo-btn {
        /* position: absolute; */
        /* top: 0; */
        /* left: 520px; */
        width: 30%;
        z-index: 30;
        h1 {
          display: block;
          background: url(https://adventurebusan.lotteworld.com/common/images/logo.png)
            no-repeat center;
          background-size: auto 100%;
          width: 205px;
          height: 58px;
        }
      }
    }
    .util {
      width: 30%;
      font-size: 14px;
      font-weight: 700;
      text-align: center;
      .user_info {
        display: inline-block;
        color: #3e3e4d;
      }
      span {
        display: inline-block;
        padding: 18px 9px;
        line-height: 1.5;
        color: #505050;
        cursor: pointer;
      }
    }
    .btn_close {
      width: 17px;
      height: 17px;
      z-index: 100;
      background: url(https://mticket.lotteworld.com/images/adventure/btn_mega_close.png)
        no-repeat 50% 50%;
      position: absolute;
      bottom: 15px;
      right: 500px;
    }
  }
`;

const ProductHeader = memo(({ loginChk }) => {
  const history = useNavigate();

  const [openList, setOpenList] = useState(false);
  let [loginStr, setLoginStr] = useState("로그인");
  let [loginState, setLoginState] = useState(false);

  const openMenu = useCallback(() => {
    setOpenList(true);
  });

  const closeMenu = useCallback(() => {
    setOpenList(false);
  });

  console.log(loginChk);

  useEffect(() => {
    // 로그인이 됐으면
    if (loginChk == true && loginState == 1) {
      console.log("111111");
      console.log(loginState);
      setLoginState(0);
      setLoginStr("로그아웃");
    } else if (loginChk == false && loginState == 0) {
      console.log("222222");
      console.log(loginState);
      setLoginState(1);
      setLoginStr("로그인");
    }
  }, [setLoginStr]);

  return (
    <ProHeaderWrap className={`${openList ? "open" : ""}`}>
      <nav>
        <ul className="gnb">
          <Link to="/" className="logo-btn">
            <h1></h1>
          </Link>
          <li className="gnbList">
            <a className="onedepth" onClick={openMenu}>
              예매
            </a>
            <ul className="twodepth">
              <li>
                <Link to="/TicketingPage">티켓 예매</Link>
              </li>
            </ul>
          </li>
          <li className="gnbList">
            <a className="onedepth" onClick={openMenu}>
              마이페이지
            </a>
            <ul className="twodepth">
              <li>
                <Link to="/TicketingPage/PaymentList">결제 내역</Link>
                <Link to="/TicketingPage/InfoChange">회원정보 변경</Link>
              </li>
            </ul>
          </li>
          <button className="btn_close" onClick={closeMenu}></button>
          <div className="util">
            <Link to="/TicketingPage/Login">{loginStr}</Link>
            {/* <span>{loginStr}</span> */}
          </div>
        </ul>
      </nav>
    </ProHeaderWrap>
  );
});

export default ProductHeader;
